/* Primary */
$color-1: hsl(263, 55%, 52%); // Moderate violet
$color-2: hsl(217, 19%, 35%); // Very dark grayish blue
$color-3: hsl(219, 29%, 14%); // Very dark blackish blue
$color-4: hsl(0, 0%, 100%);   // white

/* Neutral */
$color-5: hsl(0, 0%, 81%); // Light gray
$color-6: hsl(210, 46%, 95%); // Light grayish blue

/* Extras */
$color-7: hsl(265deg, 62%, 71%);
$color-8: hsla(0, 0%, 100%, 0.5);
$color-9: hsla(219, 29%, 14%, 0.7);